/**
 * workflow Queue
 */
import Queue from './queue';

/**
 * custom Event
 */
import EventEmitter from 'events';

/**
 * Fluder
 */
export default new class Fluder {
    /**
     * 
     * @return {object} Fluder
     */
    constructor() {
        /**
         * store handlers Map
         * @type {Object}
         */
        this._registers = {};

        /**
         * dispatch
         * @type {Array}
         */
        this._dispatchStack = [];

        /**
         * 
         */
        this._init();
    }

    _init() {
        /**
         * action payloadstoreId
         */
        this._middleware = new Queue((payload) => {
            /**
             * Store handler
             */
            this._invoke(payload);

        }, true);
    }

    /**
     * actionhandler()
     * @param  {object} payload payloadactionactionstoreId
     * @return {void}           
     */
    _invoke(payload) {
        /**
         * storeId: mapregisterhandler
         * @type {string}
         */
        let {
            storeId
        } = payload;
        /**
         * storehandler
         * @type {object}
         */
        let {
            store,
            handlers
        } = this._registers[storeId];

        /**
         * action payload
         * @type {object}
         */
        payload = payload.payload;

        /**
         * storeIdstore Maphandler
         * @type {function}
         */
        let handler = handlers[payload.type];

        if (typeof handler === 'function') {
            // Invoke store handler
            /**
             * TODO
             * resultstorecopycopyStoreImmutable
             * view-controllerresultstore
             */
            let result;
            let _result = handler.call(store, payload)
            /**
             * set Store
             * payloadchangeStore[]
             */
            // if (result !== undefined) {
            store.emitChange(payload, result);
            // }
        }
    }
    
    /**
     * ActionActionID
     */
    _startDispatch(storeId) {
        this._dispatchStack || (this._dispatchStack = []);
        this._dispatchStack.push(storeId);
        this._currentDispatch = storeId;
    }

    /**
     * ActionActionActionID
     */
    _endDispatch() {
        this._dispatchStack.pop();
        this._currentDispatch = null;
    }

    register(storeId, regs){
        this._registers[storeId] = regs;
    }

    enter(middleware){
        this._middleware.enter(middleware)
    }

    /**
     * action()
     * @param  {string} storeId store/action
     * @param  {object} action  action
     * @return {void}           
     */
    dispatch(storeId, payload) {
        /**
         * ActionStore handlerActionA-A,
         */
        if (this._currentDispatch == storeId) {
            throw Error('action '+(payload.value&&payload.value.actionType)+' __invoke__ myself!')
        }
        /**
         * ActionStore handlerActionActionA-B-C-A
         */
        if (this._dispatchStack.indexOf(storeId) != -1) {
            throw Error(this._dispatchStack.join(' -> ') + storeId + ' : action __invoke__ to a circle!');
        }
        /**
         * ActionActionID
         */
        this._startDispatch(storeId);
        /**
         * ActionActionTypeActionTypeStore handlers Mapkey
         */
        if (!payload.type) {
            throw new Error('action type does not exist in \n' + JSON.stringify(payload, null, 2));
        }
        /**
         * action 
         */
        this._middleware.execute({
            storeId,
            payload
        });
        /**
         * ActionActionActionID
         */
        this._endDispatch()
    }
}



